/* ==========================================================================
   REGLAS DE BRILLOS - EFECTOS DE ILUMINACIN POR SECCIN
   ========================================================================== */

/* ==========================================================================
   BRILLO INDIVIDUAL PARA CADA SECCIN
   ========================================================================== */

/* Hero Section - Brillo azul */
#hero::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background: radial-gradient(ellipse at center, rgba(30, 58, 138, 0.3) 0%, transparent 70%);
  pointer-events: none;
  z-index: -1;
}

/* TechStack Section - Brillo verde */
#techstack::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
